import React, {useEffect} from 'react';

import {Container} from "react-bootstrap";

import {motion} from "framer-motion";
import {animationStart, reveal} from "../utils/animation";

function DeliveryScreen() {

    const extraMenuClose = () => {
        const menu = document.querySelector('.extra-menu')

        menu.animate({
            left: '-16rem'
        }, 250)

        setTimeout(() => {menu.style.left = '-16rem'}, 249)
    }

    useEffect(() =>{
        extraMenuClose()
        window.scrollTo({top: 0, left: 0, behavior: 'smooth'});
    }, [])

    return (
        <motion.div>
            <Container id='info'>
                <motion.div
                    variants={reveal}
                    initial='hiddenVariantY'
                    animate='revealedVariantY'
                    transition={{
                        ease: 'easeIn',
                        type: 'spring',
                        staggerChildren: .05,
                        duration: 1,
                        delayChildren: animationStart,
                        delay: animationStart
                    }}
                    className='text-center my-3'
                >
                    <motion.h1></motion.h1>
                </motion.div>

                <motion.div
                    variants={reveal}
                    initial='hiddenVariantX'
                    animate='revealedVariantX'
                    transition={{
                        ease: 'easeIn',
                        type: 'spring',
                        staggerChildren: .05,
                        duration: 1,
                        delayChildren: animationStart + .25,
                        delay: animationStart
                    }}
                    className='text-left mb-3'
                >
                    <motion.h2 variants={reveal} className='mx-0 my-0'></motion.h2>

                    <motion.h4 variants={reveal} className='mx-1 my-0 p-0'>:</motion.h4>
                    <motion.h5 variants={reveal} className='mx-2 mb-3 p-0'>   
                        .</motion.h5>

                    <motion.h4 variants={reveal} className='mx-1 my-0 p-0'>:</motion.h4>
                    <motion.h5 variants={reveal} className='mx-2 mb-3 p-0'>     
                            .</motion.h5>

                    <motion.h4 variants={reveal} className='mx-1 my-0 p-0'>:</motion.h4>
                    <motion.h5 variants={reveal} className='mx-2 mb-3 p-0'> ,     ,
                            .      ,  
                             .</motion.h5>

                    <motion.h4 variants={reveal} className='mx-1 my-0 p-0'>:</motion.h4>
                    <motion.h5 variants={reveal} className='mx-2 mb-3 p-0'>    , 
                           .</motion.h5>

                    <motion.h2 variants={reveal} className='mx-0 my-0'></motion.h2>

                    <motion.h5 variants={reveal} className='mx-1 my-0 p-0'>  
                          .   :  7  .</motion.h5>
                    <motion.h5 variants={reveal} className='mx-1 mb-3 p-0'>   ,
                        -       -   
                          .</motion.h5>
                    <motion.h5 variants={reveal} className='mx-1 my-3 p-0'>*     .
                            .</motion.h5>
                    <motion.h5 variants={reveal} className='mx-1 my-0 p-0'>    ,  
                              :  ,   
                        .</motion.h5>
                    <motion.h5 variants={reveal} className='mx-1 mb-3 p-0'>       
                        ,   /,      :
                        <strong> support@unfort.ru</strong></motion.h5>
                    <motion.h5 variants={reveal} className='mx-1 mb-3 p-0'>     
                        :  ,     ,    
                        /.</motion.h5>

                    <motion.h2 variants={reveal} className='mx-0 my-0'> </motion.h2>

                    <motion.h5 variants={reveal} className='mx-1 my-0 p-0'>    :
                        <strong> 390</strong></motion.h5>
                    <motion.h5 variants={reveal} className='mx-1 my-0 p-0'>  :
                        <strong>  7  .</strong></motion.h5>
                    <motion.h5 variants={reveal} className='mx-1 my-0 p-0'>   , - 
                             -    
                        .</motion.h5>
                    <motion.h5 variants={reveal} className='mx-1 my-3 p-0'>*     . 
                           .</motion.h5>
                    <motion.h5 variants={reveal} className='mx-1 my-0 p-0'>    ,  
                             :  ,   
                        .</motion.h5>
                    <motion.h5 variants={reveal} className='mx-1 mb-3 p-0'>       
                        ,   /,      :
                        <strong> support@unfort.ru</strong></motion.h5>
                    <motion.h5 variants={reveal} className='mx-1 my-0 p-0'>     
                        :  ,     ,    
                        /.</motion.h5>
                </motion.div>
            </Container>
        </motion.div>
    );
}

export default DeliveryScreen;